<!--  -->
<template>
	<div id="ZongHeChaXun" class="height100"> 
    <v-headr :myname="myname"></v-headr>

    <div class="shadowBox" v-if="isSear_city"></div>

    <section class="aui-scrollView ">  

      <div class="aui-card-list ZongHeChaXun"> 

        <ul class="cityBox" v-if="isSear_city">   
          <li v-for="item in cityDatas"
          @click="city_item(item.cropCategoryName,item.cropCategoryCode)"  
          :class="{'active':isCityA=='item.cropCategoryName'}" 
          v-if="item.cropCategoryCode.length == 2"
          >{{item.cropCategoryName}}<span ></span></li>
        </ul>

        <ul class="cityBox" v-if="isSear_city_sub">
          <li v-for="itm in cityDatas_sub"
          @click="city_item(itm.cropCategoryName,itm.cropCategoryCode,1,thisCLass)"  
          :class="{'active':isCityA=='itm.cropCategoryName'}" 
          v-if=""
          >{{itm.cropCategoryName}}<span ></span></li>
        </ul>

                  <!-- <ul class="cityBox" v-if="isSear_city">   
                    <li v-for="item in cityDatas"
                     @click="city_item(item.cropCategoryName,item.cropCategoryCode)"  
                     :class="{'active':isCityA=='item.cropCategoryName'}" 
                     v-if="item.cropCategoryCode.length == 2"
                     >{{item.cropCategoryName}}<span ></span></li>
                  </ul>

                  <ul class="cityBox" v-if="isSear_city_sub">
                    <li v-for="itm in cityDatas_sub"
                     @click="city_search(itm,thisCLass)"  
                     :class="{'active':isCityA=='itm.cropCategoryName'}">
                     {{itm.cropCategoryName}}<span ></span></li>
                   </ul>  -->

                   <ul class="search_tool">
                    <li :class="{'active':isA==0}" @click="city_grep(0)">{{defSelect[0]}}<span class="down-arrow"></span></li><li :class="{'active':isA==1}" @click="city_grep(1)">{{defSelect[1]}}<span class="down-arrow"></span></li><li :class="{'active':isA==4}" @click="city_grep(4)">{{defSelect[2]}}<span class="down-arrow"></span></li><li :class="{'active':isA==2}" @click="city_grep(2)">{{defSelect[3]}}<span class="down-arrow"></span></li><li :class="{'active':isA==3}" @click="city_grep(3)">{{defSelect[4]}}<span class="down-arrow"></span></li>
                  </ul>

                  <!---->
                  <div style="clear: both;"></div>  

                  <div v-show="SearchList">
                   <a  href="javascript:void(0);"
                   class="aui-panel-cell"
                   v-for="key in datas"
                   @click="goInfo(key)"> 
                   <div class="aui-panel-cell-bd">
                    <h4>{{key.HxCropofferStr}}</h4>
                    <span>{{key.OfferDate}}</span>
                  </div> 
                  <div class="float-rigt">
                    <em class="jiage">{{key.Price}}{{key.UnitName}}</em>
                  </div>
                </a>

                <a href="javascript:;" @click="appendMore()"></a>
              </div>


              <div v-show="isSear">
                <div id="showLetter" style="display:none" class="showLetter"><span>A</span></div>
                <!---->
                <div class="letter" style="display:none;">
                  <ul>
                    <li><a href="javascript:;"></a></li>
                    <li><a href="javascript:;"></a></li> 
                    <li><a href="javascript:;">Z</a></li>
                  </ul>
                </div>
                <!---->
                <div class="container" id="cont">
                  <div class="city"> 
                    <!-- <span class="hot selec_city_btn"><div class="active">{{defSelect[0]}}<em>x</em></div></span> -->
                    <div class="hot hotCity">
                      <div v-for="items in RegionData" @click="city_select(items)">{{items.region}}</div>
                    </div> 
                  </div>
                </div>
              </div>


            </div> 
          </section>

        </section>
      </div>
    </template> 
    <script type="text/javascript">
    import Headr from '../../components/header.vue'   
    var search_Params = {
        iscropcategory:1,     //
        PerPageNum:100,       //
        PageNum:1,            //
        CropCategoryCode:'',  //id
        RegionCode:'',
        RegionCode:'',
        CropSourceCode:'',
        OfferDateStart:'',
        OfferDateEnd:'',
        SortType:''
    };    //

  /*const SanDa_ZhuLiang =  sessionStorage.SanDa;
  const dataList = JSON.parse(SanDa_ZhuLiang); */
  //  
  export default {
    name:'ZongHeChaXun',
    data(){
     return {
      msg:'',
      myname:{name:'', IsSubPage:true},
        thisCLass:null,     //
        isCityA:'',         //
        isA:0,              //active 0
        isSear:false,       // 
        isSear_city:false,  // 
        SearchList:true,   // 
        searchVal:null,
        cityListHeight:null,
        datas: null,
        cityDatas:null,
        isSear_city_sub:false, //
        cityDatas_sub:null,     //
        RegionData:null,
        defSelect:['','','','',''],
      }
    },
    components:{
      'v-headr':Headr,
    },
    methods:{
      ////
      goToSerch(kind){
        window.location.href="/api/JiShi/ZongHeChaXun?hotCity="+kind;
      },
      getUrlParam(key) {
      // 
      var url = window.location.search;
        // 
        var reg = new RegExp("(^|&)" + key + "=([^&]*)(&|$)");
        // 
        var result = url.substr(1).match(reg);
        //
        return result ? decodeURIComponent(result[2]) : null;
      },
      initIndexList:function(){
        return;
        var _baseThis = this;
        console.warn(""); 
        var cityList=[{key:'A',data:['','','','','','','','','','','','','','','','','','','','','','','','','','','','','','','','','']}]
        var hotCity=['','','','','','','','',''];
        var _this = this;
        $(function () {
          init();
      // 
      $('body').on('click', '.city-list p', function () {
        var data = $(this).text();
          // saveHistory(data); //   

          _baseThis.isSear=false;       // 
          _baseThis.isSear_city=false;  // 
          _baseThis.SearchList=true;   // 
          _baseThis.city_item(data);
          console.dir(_baseThis)
        });

      $('.hot.hotCity').on('click', 'div', function () {
        var data = $(this).text();
        $(this).addClass("active").siblings().removeClass("active");
        saveHistory(data);  
          _this.searchCity(data);  // search 

           _baseThis.isSear=false;       // 
          _baseThis.isSear_city=false;  // 
          _baseThis.SearchList=true;   // 
          _baseThis.city_item(data);
          console.dir(_baseThis)

        });

    })

        function bindHistoryClick() {
          $('.history .hot').on('click', 'div', function () {
        // var data = $(this).text();
        // saveHistory(data);
        var data = $(this).text();
        $(this).addClass("active").siblings().removeClass("active");
        saveHistory(data);  
        _this.searchCity(data);  // search  
        
      });

        }

        function clearHistory() {
          localStorage.setItem('hotelHistory', '');
          initHistory();
        }

        function saveHistory(data) {
          var hotelHistory = localStorage.getItem('hotelHistory');
          if(!hotelHistory){
            hotelHistory = '';
          }
          if(hotelHistory.indexOf(data) > -1) {
            var arr = hotelHistory.split(',');
            var newArr = [];
            $.each(arr, function (i, item) {
              if(item !== data) {
                newArr.push(item);
              }
            })
            hotelHistory = newArr.join(',');
          }
          localStorage.setItem('hotelHistory', data + ',' + hotelHistory);
          initHistory();
        }

        function init() {
          $('.city').html('');
          var hotHtml = '';
          hotHtml += '<div class="tips" id="1"></div>';
          hotHtml += '<div class="hot hotCity">';
          $.each(hotCity, function (i, item) {
            hotHtml += '<div>' + item + '</div>'
          })
          hotHtml += '</div>';
          hotHtml += '<div class="history"></div>';
          $('.city').append(hotHtml);

      // initHistory();
      
      var html = '';
      $.each(cityList, function (i, item) {
        html += '<div class="city-list"><span class="city-letter" id="' + item.key + '1">' + item.key + '</span>';
        $.each(item.data, function (j, data) {
          html += '<p>' + data + '</p>';
        })
        html += '</div>';
      })
      $('.city').append(html);
    }

  function initHistory() {  // 
    return;
    var hotelHistory = localStorage.getItem('hotelHistory');
    var history = '';
    history += '<div class="tips" id="1"><div class="clear-history" onClick="clearHistory();"></div></div>';
    history += '<div class="hot">';
    if(hotelHistory) {
      $.each(hotelHistory.split(','), function (i, item) {
        if(item) {
          history += '<div>' + item + '</div>'
        }
      })
    }
    history += '</div>';
    $('.history').html(history);
    bindHistoryClick();
  }
  
  ; (function ($) {

    $('.letter').bind("touchstart touchmove", function (e) {
      var top = $(window).scrollTop();
          e.preventDefault();//
          var touch = e.touches[0];
          var ele = document.elementFromPoint(touch.pageX, touch.pageY - top);
          console.dir("tag:"+ele.tagName)
          if (ele.tagName === 'A') {
            var s = $(ele).text();
            $(window).scrollTop($('#' + s + '1').offset().top)
            $("#showLetter span").html(s.substring(0, 1));

            $("#showLetter").show().delay(500).hide(0);
          }
        });

    $('.letter').bind("touchend", function (e) {
      $("#showLetter").hide(0);
    });

  })(Zepto);  

// 

},
       /**
       * get
       * @param url
       * @param data
       * @returns {Promise}
       */
       getdata:function(url,params={}){
        return new Promise((resolve,reject) => {
          this.axios.get(url,{
            params:params
          })
          .then(response => {
            resolve(response.data);
          })
          .catch(err => {
            reject(err)
          })
        })

      },
       zongheSearch:function(keyword){  // 

        console.log(keyword)
      },
      scroll(person) {


      },

       initList:function(){   // list 
          // http://192.168.112.37:8088/flxoa/market/active?type=huoyue
          // PerPageNum 
          // PageNum 

          this.getdata('/api/flxoa/CropOffer',search_Params).then((response) => {
            // console.log(response[0].data)
            this.datas = response[0].data;
          });

          //
          this.getdata('/api/flxoa/CropCategory').then((response) => {
            this.cityDatas = response; 
          });

          


        },
        city_grep(inx){

          this.isA = inx; 

        if(inx==0){ //
          this.SearchList =!this.SearchList;
          this.isSear = !this.SearchList;  
          this.isSear_city = false;  
        }
        if(inx==1){  //
          // CropCategoryID

          this.SearchList =true;
          this.isSear_city = !this.isSear_city;  
          this.isSear_city_sub = false;  //
        }
        if(inx==2){ //


          // this.SearchList =true;
          // this.isSear_city = !this.isSear_city;  
        }
        if(inx==3){ //
          // this.SearchList =true;
          // this.isSear_city = !this.isSear_city;  
        } 


        if(inx==4){ //

          this.isSear_city_sub = true;   //
          this.SearchList =  this.SearchList;    //
          this.isSear_city = false;   //
        } 


        
      },
      city_search:function(items){  //
        console.log(items)
      },
      city_item(str,qcode,type,thisCityCode){ //qcode  id  type

        this.isCityA = str;
        console.log(str);

        console.log(qcode);
        if(qcode==null && type !=null){ // 
          this.getdata('/api/flxoa/CropOffer',{iscropcategory:'1',PerPageNum:100,PageNum:1}).then((response) => {
            const list = response[0].data;
            console.log(list)
            const filtrList = list.filter(function(item){
             var itemsStr;
             if(item.HxCropofferStr.indexOf(str)> -1){
              console.log(item); 
              return item;
            }
          });   
            console.log(filtrList)
            this.datas = filtrList; 

          })
          this.SearchList =true;    //
          this.isSear_city = !this.isSear_city;   //   
          this.isSear_city_sub = false;  //
        }

        if(qcode!=null && type!=null){  
          this.defSelect[2] = str;
          console.log("ID:"+thisCityCode);
          console.log("")
          const Rests = this.cityDatas;
          console.log(Rests);
          const filtrList_XI = Rests.filter(function(item){
           var itemsStr;
           if(item.parentId === qcode ){
                // console.log(item); 
                return item;
              }
            });  
          search_Params.iscropcategory = 0; //0
          // search_Params.ParentID = null;
          search_Params.CropCategoryCode = qcode;
          this.getdata('/api/flxoa/CropOffer',search_Params).then((response) => {
            const list = response[0].data; 
            const filtrList_Data = list.filter(function(item){
             var itemsStr;
             if(item.HxCropofferStr.indexOf(str)> -1){
              console.log(item); 
              return item;
            }
          });   
            console.log(filtrList_Data)
            this.datas = filtrList_Data; 

          }) 




          this.cityDatas_sub = filtrList_XI;

          this.SearchList =true;    //
          this.isSear_city = false;   //   
          this.isSear_city_sub = false;  //

        }

        if(qcode!=null && type==null){  
          this.defSelect[1] = str;
          const Rests = this.cityDatas;
          const filtrList = Rests.filter(function(item){
           var itemsStr;
           if(item.parentId === qcode ){
                // console.log(item); 
                return item;
              }
            }); 

          // iscropcategory: 0 
          
          this.thisCLass = qcode; //id
          console.log("id:"+qcode);
// {iscropcategory:0,PerPageNum:100,PageNum:1,ParentID:qcode}   
// /api/flxoa/CropOffer?iscropcategory=1&PerPageNum=100&PageNum=1&CropCategoryID
          search_Params.iscropcategory = 0; //0
          search_Params.CropCategoryCode = null;
          search_Params.ParentID = qcode;
          this.getdata('/api/flxoa/CropOffer',search_Params).then((response) => {
            const list = response[0].data;  
            console.log(list)
            this.datas = list; 

          }) 


this.cityDatas_sub = filtrList;

          this.SearchList =true;    //
          this.isSear_city = false;   //   
          this.isSear_city_sub = false;  //

        }


      },
    searchBuild:function(qid){  //qid url
      console.log("------------hotCity------------");

      this.getdata('/api/flxoa/CropOffer',{iscropcategory:'1',PerPageNum:100,CropCategoryCode:qid}).then((response) => {
        const queryList = response[0].data; 
        this.datas = queryList;
          this.isSear=false;       // 
          this.isSear_city=false;  // 
          this.SearchList=true;   //  
        });
    },
    searchCity:function(cityName){

          search_Params.iscropcategory = 0; //0
          search_Params.CropCategoryCode = null;
          search_Params.ParentID = null;


      this.getdata('/api/flxoa/CropOffer',{iscropcategory:'1',PerPageNum:200}).then((response) => {
        const queryList = response[0].data;  console.log("searchCity:")
        console.dir(queryList)
        const filtrList = queryList.filter(function(item){ 
         if(item.RegionName.indexOf(cityName) > -1){
                  // console.log(item); 
                  return item;
                }
              }); 

        this.datas = filtrList; 

            this.isSear=false;       // 
            this.isSear_city=false;  // 
            this.SearchList=true;   //   
          });

    },
    goInfo:function(keys){
      console.log(keys.RegionCode);
      console.log(keys.CropCategoryCode);
                // console.log("go:"+Region)
                // console.log("go:"+CropCategory)
                this.$router.push({ name: 'SanDaZhuLiangXQ', query: { Region: keys.RegionCode,CropCategory: keys.CropCategoryCode,Price:keys.Price,CropOfferStr:keys.HxCropofferStr }})
              },
              searchDetail:function(qcode){
                this.getdata('/api/flxoa/CropOffer',{iscropcategory:1,PerPageNum:100,PageNum:1,CropCategoryCode:qcode}).then((response) => {
                  const list = response[0].data;  
                  this.datas = list;  
                }) 
              },
    city_select:function(regionItem){ //RegionCode   id
      ///CropOffer?iscropcategory=0&PerPageNum=100&PageNum=1&ParentID=02
      this.defSelect[0]  = regionItem.region;
      search_Params.RegionCode = regionItem.regionCode;
      this.getdata('/api/flxoa/CropOffer',search_Params).then((response) => {
        const list = response[0].data;  
        this.datas  = list;  

          this.isSear =false;       // 
          this.isSear_city =false;  // 
          this.SearchList  =true;   //   
        })  
    },    

  },
  mounted(){
    this.initIndexList();  
      this.initList(); //   

      this.getdata('/api/flxoa/Region').then((response) => {
        console.log("Region----------------");
        this.RegionData = response
        console.dir(this.RegionData);
      }) 

      let pageValue = this.getUrlParam("hotCity");
      if(pageValue!=null){
        // this.searchBuild(pageValue);
        this.searchCity(pageValue);
      }

      let hotID = this.getUrlParam("hotCityID");
      if(hotID!=null){ 
        this.searchDetail(hotID);
      }
      

    }
  }

/*

 
http://op.juhe.cn/onebox/news/query
q=gdp&dtype=&key=3466a2957c5095c8887ed3423742d909
GET


*/

</script>


<style type="text/css">
.height100{height:100%}
.shadowBox{position:fixed;z-index:1;width:100%;height:100%;background:#000;opacity:.5}
.aui-card-list{position:relative}
.cityBox{position:absolute;top:30px;z-index:2;width:100%;height:auto;background:#fff}
.cityBox li{height:30px;border-bottom:1px solid #ccc;text-indent:20px;font-size:14px;line-height:30px}
.cityBox li>span{float:right;margin-right:10px;width:30px;height:30px;color:#fff;line-height:30px}
.cityBox li.active>span{color:#fb8940}
.search_tool{position:relative;z-index:1}
.bigCity { background: #aabbb5;color: #fff;height: 20px;line-height: 20px;}
.search_tool > li { width:19%; overflow:hidden;}
.search_tool > li  .down-arrow { margin-right:0;}
.selec_city_btn { display:block; position:relative; width:auto; }
.selec_city_btn .active { color:#24b35b; border:1px solid #24b35b; position:relative; }
.selec_city_btn em { position:absolute; top:-10px; right:-10px; width:10px; height:10px;color:red;}
</style>


