<!--  -->
<template>
	<div id="WoDeGuanZhu">
		<v-headr :myname="myname"></v-headr>
		<div class="my-concern-container">
			<!--  -->
	        <!-- <ul class="my-concern-warp aui-card-list" v-for="item in datas">
	            <li class="my-concern-list aui-panel-cell">
	                <div class="my-concern-pic "></div>
	                <div class="aui-panel-cell-bd">
	                    <h4>{{item.src}}</h4>
	                </div>
	                <div class="right_tex my-concerned"> </div>
	            </li>
	        </ul> -->

	        <ul class="my-concern-warp aui-card-list">
	            <li  v-for="item in datas"
	            class="my-concern-list aui-panel-cell">
	                <div class="my-concern-pic "></div>
	                <div class="aui-panel-cell-bd">
	                    <h4>{{item.SourceName}}</h4>
	                </div>
	                <div class="right_tex my-concerned"
	                 @click="followRemove(item.FollowID)"> </div>
	            </li> 
	        </ul>
	    </div>




	    <div class="add-more-con">
	        <a class="add-more" href="javascript:;" @click="addMore()">
	            <span class="add-pic"></span>
	            <span class="add-btu"></span> 
	        </a>
	    </div>
	</div>
</template>

<script type="text/javascript">
	import Headr from '../../components/header.vue' 
	export default {
		name:'WoDeGuanZhu',
		data(){
			return {
				msg:'',
				myname:{
					name:'',
					IsSubPage:true
				},
				datas:null 
			}
		},
		components:{
			'v-headr':Headr,
		},
		methods:{			
			followRemove:function(sorcId){ 
				var this_ = this;
				//var user_id = sessionStorage.userId;
				//if(user_id == null) user_id = '000000';
				console.log("ID"+sorcId)
				this.axios.get('/api/flxoa/FollowDel?SourceCode='+sorcId)
		        .then(function(response){
		        	//console.log(""+response)
		        	//console.log(""+JSON.stringify(response) )
		        	if(response.data.Msg == ""){
		        		this_.init_gz()
		        		this_.$toast("");
		        	}
		        	
		          
		        })
		        .catch(function(error){
		          console.log(error);
		        }); 
			},

			init_gz(){
				var scThis = this; 
				var user_id = sessionStorage.userId; 
				this.axios.get('/api/flxoa/Follow?tokenid='+user_id+'&Type=1').then(function(response){ 
					console.log('-----------------------------------------')
					console.dir(response.data[0].data);
	                scThis.datas = response.data[0].data;
	            });  
			},
			addMore(){
				this.$router.push({name:'LaiYuan'}) //
				 
			},



		},
		mounted(){
			//this.islogin()
			this.init_gz();
            this.tabIndex = 2;


		},
		created(){

		},


	}
</script>


